.departure {
  border: 1px solid var(--color-rinse);
  border-radius: 8px 0 0 8px;
  height: 64px;
  background-color: var(--color-light);
  transition: 0.3s;
  user-select: none;

  &__input {
    position: relative;
    display: flex;
    align-items: flex-end;
    padding: 32px 10px 10px 16px;
    width: 165px;
    height: 100%;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    line-height: 20px;
    transition: 0.3s;
  }

  &__title {
    position: absolute;
    left: 16px;
    top: 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-great);
    transition: 0.3s;
  }

  &__body {
    overflow-x: hidden;
    overflow-y: scroll;
    max-height: 356px;
  }

  &:hover {
    border: 1px solid var(--color-great);
    border-radius: 8px;
    transition: 0.3s;
  }

  @include tablet {
    border-radius: 8px 0 0 8px;
  }

  @include mobile {
    border-radius: 8px 8px 0 0;
  }
}

.departure-drop {
  list-style-type: none;
  margin-top: 0;
  padding-left: 0;
  width: 229px;

  &__item {
    padding: 10px 10px 10px 20px;

    &:hover {
      background-color: var(--color-violet);
    }
  }
}
