.tours {
  &__wrapper {
    display: none;
  }

  &__submenu {
    position: absolute;
    top: 94px;
    z-index: 0;
    border-radius: 8px;
    padding: 16px 18px;
    width: 270px;
    box-shadow: 0 0 5px rgb(0 0 0 / 5%), 0 10px 10px rgb(0 0 0 / 10%);
    background-color: var(--color-light);

    &-link {
      display: flex;
      padding: 9px 20px;
      transition: 0.2s;

      &:hover {
        background-color: var(--color-violet);
      }
    }

    @include tablet {
      top: 92px;
    }
  }

  &:hover &__wrapper {
    display: block;
  }
}
